import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { title: 'Новости которые мы заслужили' }
  },
  {
    path: '/last',
    name: 'Last',
    component: () => import('../views/Last.vue'),
    meta: { title: 'Последние события | Новости которые мы заслужили' }
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import('../views/Contact.vue'),
    meta: { title: 'Алексей Суздаленко веб-андроид разработчик' }
  },
  {
    path: '/detail/:id',
    name: 'Detaii',
    component: () => import('../views/Detail.vue'),
  }
];

const router = createRouter({history: createWebHistory(process.env.BASE_URL), routes});

router.beforeEach((to) => {
  if(to.meta.title) document.title = to.meta.title;
  document.documentElement.setAttribute('description', to.meta.title + ' Suzdalenko');
  console.log(to);
});

export default router

