<template>
  <footer id="colophon" class="site-footer">
    <div class="container">
      <div class="site-info">
        <a target="blank" href="https://diseno-cantabria.web.app/"><h6 style="font-family: 'Herr Von Muellerhoff';color: #ccc;font-weight:300;text-align: center;margin-bottom:0;margin-top:0;line-height:1.4;font-size: 46px;">Suzdalenko</h6></a>
        <a target="blank" href="https://diseno-cantabria.web.app/">&copy; Google - Diseño Web Cantabria - {{ currentData }}</a>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  data(){
    return {
      currentData : ''
    }
  },
  created() {
    setInterval(this.getMiCurrentTime, 1100)
  },
  methods:{
    getMiCurrentTime(){
      var currentdate = new Date();
      let minutes = currentdate.getMinutes();
      if(minutes < 10) minutes = '0'+minutes;
      let second = currentdate.getSeconds();
      if(second < 10) second = '0'+second;
      var datetime = currentdate.getDate() + "/"
          + (currentdate.getMonth()+1)  + "/"
          + currentdate.getFullYear() + " @ "
          + currentdate.getHours() + ":"
          + minutes + ":"
          + second;
      this.currentData = datetime
    }
  },
  computed: {

  }
}
</script>